      two_bpr: [''],
      two_lpr: [''],
      two_tsefal_index: [''],
      two_head_okr: [''],
      two_setch_mikhur: [''],
      two_mozochok: [''],
      two_intraokul_rozmir: [''],
      two_sdgk: [''],
      two_diam_zuv: [''],
      two_okr_zuvota: [''],
      two_shlunok: [''],
      two_nurka_right: [''],
      two_nurka_left: [''],
      two_dovz_stegna: [''],
      two_vel_bedr_kist: [''],
      two_dozv_mal_bedr_kist: [''],
      two_stopa: [''],
      two_dovz_plecha: [''],
      two_dovz_likt: [''],
      two_sertse_mm: [''],
      two_dovz_promenevoi: [''],
      two_kust: [''],
      two_luts_rozshilunu: [' '],
      two_nosova_kistka: [' '],
      two_rozmir_skladku: [' '],
      two_kilkist_vod: [''],
      two_local_plats: [''],
      two_nuz_platsenty: ['   . '],
