import { Component, OnInit } from '@angular/core';
import { BlankBaseComponent } from '../../../shared/components/blank-base/blank-base.component';
import { FormBuilder } from '@angular/forms';
import { DateService } from '../../../shared/services/date.service';
import { PrintDataService } from '../../../shared/services/print-data.service';
import { Router } from '@angular/router';
import { UzdProtocolOneUzStructure } from '../../../shared/structures/uzd-protocol-one-uz.structure';

@Component({
  selector: 'app-uzd-protocol-one-uz',
  templateUrl: './uzd-protocol-one-uz.component.html',
  styleUrls: ['./uzd-protocol-one-uz.component.scss']
})
export class UzdProtocolOneUzComponent extends BlankBaseComponent implements OnInit {

  constructor(
    fb: FormBuilder,
    date: DateService,
    data: PrintDataService,
    private router: Router
  ) {
    super(fb, date, data);
    super.ngOnInit();
  }

  ngOnInit(): void {
    this.options = {
      title: `
        
       - (11 . + 1  - 13 . + 6 )`,
      fontSize: 18,
      showDoctors: true,
      borderInValues: false,
      strongTitles: true,
      showEmptyRows: true
    };

    this.form = this.fb.group({
      one_date: [this.currentDate],
      one_pib: [''],
      one_om: [''],
      one_gastatsia: [''],
      one_termin: [''],
      one_method: [''],
      one_dovz: [''],
      one_shuruna: [''],
      one_pered_zad: [''],
      one_structura_biomentiyu: [' '],
      one_tonus: [' '],
      one_local_hipertonus: [''],
      one_u_poroz_vizial: ['    '],
      one_diamert_egg: [' .'],
      one_kilkist_embrioniv: [''],
      one_vidsharuvannya: [''],
      one_kupriko: [' '],
      one_biparietal: [' '],
      one_skull_bones: [''],
      one_okruznist: [' '],
      one_ser_diametr: [' '],
      one_dov_stegna: [' '],
      one_komirts_prostir: [' '],
      one_nose_kist: ['  (  )'],
      one_hearth: [''],
      one_hearth_bom: [''],
      one_chss: [' ./'],
      one_pered_cherevna: [''],
      one_ruh_ploda: [''],
      one_local_horionu: [''],
      one_tovshuna: [' '],
      one_structura: ['/'],
      one_shuyka: [' '],
      one_tserv_canal: [' '],
      one_diam_vnutrishnogo: [' '],
      one_stan_prudatkiv: ['/'],
      one_osobluvosti: [''],
      one_results: [''],
      doctors: ['']
    });

    this.data.blankData.subscribe(options => {
      if (options && options.data && Object.keys(options.data).length !== 0) {
        this.form.patchValue(options.data);
      }
    });
  }

  printCurrentPage() {
    if (this.form.valid) {
      const value = this.form.value;
      this.data.updateData({
        ...this.options,
        data: value,
        structure: UzdProtocolOneUzStructure,
        route: 'blank/uzd-protocol-one-uz'
      });

      this.router.navigateByUrl('/print');
    }
  }

}
